import './App.css';
import React from 'react';
import words from "./words";

class App extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            words: [],
            out_words: [],
            word: "hello",
        };
    }

    componentDidMount() {
        const words_arr = words().split('\n').filter(el => {
                return el != null && el !== '';
            });

        this.setState({words: words_arr})
    }

    show_another_word = () => {
        let words_arr = this.state.words.filter(el => {
            return !this.state.out_words.includes(el)
        });


        if (words_arr.length === 0) {
            console.log(words_arr.length)
            words_arr = this.state.words
            this.state.out_words.length = 0;
        }

        const word = words_arr[Math.floor(Math.random() * words_arr.length)];
        this.setState({
            word: word,
            out_words:  [...this.state.out_words, word],
        })
    }

    number_of_rest_words = () => {
        return this.state.words.length - this.state.out_words.length
    }

    render() {
        return (
            <div className="App">
                <p className="small">
                    Количество слов: {this.number_of_rest_words()}
                </p>
                <div>{this.state.word}</div>
                <div
                    className="btn btn-outline-light"
                    onClick={this.show_another_word}>
                    next
                </div>
            </div>
        );
    }
}

export default App;
